import React from 'react';
import { Radio, Button } from 'antd';
import { I18n } from 'react-redux-i18n';
import { SearchOutlined } from '@ant-design/icons';

const FilterRadioColumn = ({ setSelectedKeys, selectedKeys, confirm, filters, clearFilters }) => {
  const radioStyle = {
    display: 'block',
    height: '30px',
    lineHeight: '30px',
  };
  return <div style={{ padding: 8 }}>
    <Radio.Group
      style={{ display: 'block' }}
      value={selectedKeys[0]}
      onChange={(e) => setSelectedKeys(e.target.value ? [e.target.value] : [])}
    >
      {filters.map(item => <Radio
        key={item.value}
        value={item.value}
        style={radioStyle}
      >
        {item.text}
      </Radio>)}
    </Radio.Group>
    <Button
      type="primary"
      onClick={() => confirm()}
      icon={<SearchOutlined />}
      size="small"
      style={{ width: 90, marginRight: 8 }}
    >
      {I18n.t('common.search')}
    </Button>
    <Button onClick={() => clearFilters()} size="small" style={{ width: 90 }}>
      {I18n.t('common.reset')}
    </Button>
  </div>;
};

export default FilterRadioColumn;