import { confirmationAction, notificationError } from 'utils/helpers';
import { TABLE_SIZE } from 'constants/table';
import { get } from 'lodash';
const PREFIX = 'Users';

export const GET_DATA_REQUEST = `${PREFIX}/GET_DATA_REQUEST`;
export const GET_DATA_SUCCESS = `${PREFIX}/GET_DATA_SUCCESS`;
export const GET_DATA_FAILURE = `${PREFIX}/GET_DATA_FAILURE`;

export const getData = (page = 1, filters = {}, sorter = {}) => {
  return async (dispatch, getState, api) => {
    dispatch({ type: GET_DATA_REQUEST });
    try {
      const {
        auth: { token }
      } = getState();
      const filter = new KazFilter({
        position: page * TABLE_SIZE - TABLE_SIZE,
        countFilter: TABLE_SIZE,
        orders: [],
        items: []
      });
      if (get(filters, 'login.0', null) !== null) filter.items.push(new FilterItem({
        field: 'login',
        value: get(filters, 'login.0', null),
        condition: FilterCondition.CONTAIN,
        fType: FilterFieldType.STRING
      }));
      const result = await api.MrkAdminServiceClient.getMrkUserPage(token, filter);
      dispatch({
        type: GET_DATA_SUCCESS,
        payload: {
          ...result,
          page: page,
          filters: filters,
          sorter: sorter
        }
      });
    } catch (error) {
      notificationError(error, 'getMrkUserPage');
      dispatch({ type: GET_DATA_FAILURE });
    }
  };
};

export const CREATE_OR_UPDATE_REQUEST = `${PREFIX}/CREATE_OR_UPDATE_REQUEST`;
export const CREATE_OR_UPDATE_SUCCESS = `${PREFIX}/CREATE_OR_UPDATE_SUCCESS`;
export const CREATE_OR_UPDATE_FAILURE = `${PREFIX}/CREATE_OR_UPDATE_FAILURE`;

export const createOrUpdate = values => {
  return async (dispatch, getState, api) => {
    dispatch({ type: CREATE_OR_UPDATE_REQUEST });
    try {
      const {
        auth: { token },
        users: { page, data, filters, sorter }
      } = getState();
      const result = await api.MrkAdminServiceClient.changeMrkUser(token, new MrkUser(values), values.password, null);
      const id = get(values, 'id', null);
      dispatch({
        type: CREATE_OR_UPDATE_SUCCESS,
        payload: id !== null ? [...data.map(item => {
          if (id === item.id) return result;
          return item;
        })] : data
      });
      if (id === null) dispatch(getData(page, filters, sorter));
      return true;
    } catch (error) {
      notificationError(error, 'changeMrkUser');
      dispatch({ type: CREATE_OR_UPDATE_FAILURE });
      return false;
    }
  };
};

export const REMOVE_REQUEST = `${PREFIX}/REMOVE_REQUEST`;
export const REMOVE_SUCCESS = `${PREFIX}/REMOVE_SUCCESS`;
export const REMOVE_FAILURE = `${PREFIX}/REMOVE_FAILURE`;

export const remove = id => {
  return async (dispatch, getState, api) => {
    const fn = async () => {
      dispatch({ type: REMOVE_REQUEST });
      try {
        const {
          auth: { token },
          users: { page, filters, sorter }
        } = getState();
        await api.MrkAdminServiceClient.changeMrkUser(token, null, null, id);
        dispatch({
          type: REMOVE_SUCCESS
        });
        dispatch(getData(page, filters, sorter));
      } catch (error) {
        notificationError(error, 'changeMrkUser');
        dispatch({ type: REMOVE_FAILURE });
      }
    };
    confirmationAction(fn, null, null);
  };
};