import React, { useState, useRef } from 'react';
import { Modal, notification } from 'antd';
import { I18n } from 'react-redux-i18n';
import { get, map } from 'lodash';
import { UserOutlined, LockOutlined } from '@ant-design/icons';
import { formLayout } from 'constants/form';
import { Formik } from 'formik';
import { Form, FormItem, Input, InputNumber, Select } from 'formik-antd';
import { mrkAlmexSysUser } from 'constants/schemes';

const MrkAccountModal = ({ show, hideModal, entity, onOk }) => {
  const [saving, setSaving] = useState(false);
  const form = useRef(null);

  return <Modal
    title={I18n.t('Modals.MrkAlmexSysUser')}
    visible={show}
    onOk={() => {
      form.current.handleSubmit();
      if (!form.current.isValid) notification.error({
        message: I18n.t('common.error'),
        description: I18n.t('common.form_is_invalid')
      });
    }}
    centered
    okButtonProps={{
      loading: saving
    }}
    onCancel={hideModal}
  >
    <Formik
      innerRef={form}
      initialValues={{
        ...entity,
        password: null
      }}
      validateOnMount={true}
      validationSchema={mrkAlmexSysUser()}
      onSubmit={async (values) => {
        try {
          setSaving(true);
          const result = await onOk(values);
          if (result) hideModal();
          setSaving(false);
        } catch (error) {
          setSaving(false);
        }
      }}
    >
      {({ values }) => <Form {...formLayout}>
        <FormItem
          name="login"
          label={I18n.t('MrkAlmexSysUser.login')}
          labelAlign="left"
          required
        >
          <Input
            name="login"
            prefix={<UserOutlined />}
            placeholder={I18n.t('MrkAlmexSysUser.login')}
          />
        </FormItem>
        <FormItem
          name="password"
          label={I18n.t('SignIn.password')}
          labelAlign="left"
          required={get(values, 'id', null) === null}
        >
          <Input.Password
            name="password"
            prefix={<LockOutlined />}
            placeholder={I18n.t('SignIn.password')}
          />
        </FormItem>
        <FormItem
          name="usePriority"
          label={I18n.t('MrkAlmexSysUser.usePriority')}
          labelAlign="left"
          required
        >
          <InputNumber
            name="usePriority"
            placeholder={I18n.t('MrkAlmexSysUser.usePriority')}
            min={0}
            max={999}
          />
        </FormItem>
        <FormItem
          name="allowedForType"
          label={I18n.t('MrkAlmexSysUser.allowedForType')}
          labelAlign="left"
          required
        >
          <Select name="allowedForType">
            {map(MrkAccountType, (value, key) => <Select.Option key={key} value={value}>{I18n.t(`MrkAccountType.${key}`)}</Select.Option>)}
          </Select>
        </FormItem>
      </Form>}
    </Formik>
  </Modal>;
};

export default MrkAccountModal;