import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import Table from 'components/Table';
import { Checkbox } from 'antd';
import { getData, createOrUpdate } from 'redux/actions/accounts';
import { TABLE_SIZE, searchProps, filterRadioProps } from 'constants/table';
import { I18n } from 'react-redux-i18n';
import { uniqueId, get, map, invert } from 'lodash';
import { showModal } from 'react-redux-modal-provider';
import { MrkAccountModal } from 'components/Modals';

const Content = ({ data, count, page = 1, filters, sorter, loading, getData, createOrUpdate }) => {

  useEffect(() => {
    getData();
  }, []);

  const CellChecked = checked => <Checkbox checked={checked} onChange={() => { }} />;

  const filtersMenu = [
    { text: I18n.t('filters.active'), value: 'true' },
    { text: I18n.t('filters.not_active'), value: 'false' }
  ];

  return <Table
    dataSource={data}
    count={count}
    loading={loading}
    rowKey={() => uniqueId('rowKey_')}
    pagination={{
      defaultPageSize: TABLE_SIZE,
      total: count,
      current: page,
      onChange: (page) => getData(page, filters, sorter)
    }}
    onRow={(record) => {
      return {
        onClick: () => showModal(MrkAccountModal, {
          id: record.accountId,
          onOk: createOrUpdate
        })
      };
    }}
    onChange={(_, filters, sorter) => getData(1, filters, sorter)}
    columns={[
      {
        title: I18n.t('MrkAggregateAccountInfo.name'),
        dataIndex: 'name',
        key: 'name',
        ...searchProps(get(filters, 'name', null), I18n.t('MrkAggregateAccountInfo.name'))
      },
      {
        title: I18n.t('MrkAggregateAccountInfo.code'),
        dataIndex: 'code',
        key: 'code',
        width: 150,
        ...searchProps(get(filters, 'code', null), I18n.t('MrkAggregateAccountInfo.code'))
      },
      {
        title: I18n.t('MrkAggregateAccountInfo.organization'),
        dataIndex: 'organization',
        align: 'center',
        width: 130,
        render: CellChecked,
        ...filterRadioProps(get(filters, 'organization', null), filtersMenu)
      },
      {
        title: I18n.t('MrkAggregateAccountInfo.contragent'),
        dataIndex: 'contragent',
        align: 'center',
        width: 120,
        render: CellChecked,
        ...filterRadioProps(get(filters, 'contragent', null), filtersMenu)
      },
      {
        title: I18n.t('MrkAggregateAccountInfo.signed'),
        dataIndex: 'signed',
        align: 'center',
        width: 130,
        render: CellChecked,
        ...filterRadioProps(get(filters, 'signed', null), filtersMenu)
      },
      {
        title: I18n.t('MrkAggregateAccountInfo.blocked'),
        dataIndex: 'blocked',
        align: 'center',
        width: 140,
        render: CellChecked,
        ...filterRadioProps(get(filters, 'blocked', null), filtersMenu)
      },
      {
        title: I18n.t('MrkAggregateAccountInfo.confirmed'),
        dataIndex: 'confirmed',
        align: 'center',
        width: 160,
        render: CellChecked,
        ...filterRadioProps(get(filters, 'confirmed', null), filtersMenu)
      },
      {
        title: I18n.t('MrkAggregateAccountInfo.accType'),
        dataIndex: 'accType',
        align: 'center',
        width: 100,
        render: accType => I18n.t(`MrkAccountType.${invert(MrkAccountType)[accType]}`),
        ...filterRadioProps(get(filters, 'accType', null), map(MrkAccountType, (value, key) => ({ text: I18n.t(`MrkAccountType.${key}`), value: `MrkAccountType.${key}` })))
      }
    ]}
  />;
};

const mapStateToProps = state => ({
  loading: state.accounts.isFetching,
  data: state.accounts.data,
  count: state.accounts.count,
  filters: state.accounts.filters,
  sorter: state.accounts.sorter,
  page: state.accounts.page
});
const mapDispatchToProps = dispatch =>
  bindActionCreators(
    {
      getData,
      createOrUpdate
    },
    dispatch
  );

export default connect(mapStateToProps, mapDispatchToProps)(Content);


