import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import Table from 'components/Table';
import { notification } from 'antd';
import { getData, createOrUpdate, remove } from 'redux/actions/users';
import { TABLE_SIZE, searchProps } from 'constants/table';
import { I18n } from 'react-redux-i18n';
import { uniqueId, get } from 'lodash';
import { showModal } from 'react-redux-modal-provider';
import { DeleteOutlined, UserAddOutlined } from '@ant-design/icons';
import { MrkUserModal } from 'components/Modals';

const Content = ({ data, count, page = 1, filters, sorter, loading, getData, createOrUpdate, remove }) => {

  useEffect(() => {
    getData();
  }, []);

  return <Table
    dataSource={data}
    count={count}
    loading={loading}
    rowKey={() => uniqueId('rowKey_')}
    pagination={{
      defaultPageSize: TABLE_SIZE,
      total: count,
      current: page,
      onChange: (page) => getData(page, filters, sorter)
    }}
    onChange={(_, filters, sorter) => getData(1, filters, sorter)}
    onRow={(record) => {
      const key = uniqueId('notification_');
      return {
        onClick: () => record.deleteDate !== -1 ? notification.info({
          key: key,
          duration: 3,
          message: I18n.t('notification.info'),
          description: I18n.t('notification.MrkUser_removed', { name: record.login })
        }) : showModal(MrkUserModal, {
          entity: record,
          onOk: createOrUpdate
        })
      };
    }}
    columns={[
      {
        title: I18n.t('MrkUser.login'),
        dataIndex: 'login',
        key: 'login',
        ...searchProps(get(filters, 'login', null), I18n.t('MrkAlmexSysUser.login'))
      },
      {
        title: I18n.t('MrkUser.firstName'),
        dataIndex: 'firstName',
        key: 'firstName',
        width: 200
      },
      {
        title: I18n.t('MrkUser.lastName'),
        dataIndex: 'lastName',
        key: 'lastName',
        width: 200
      },
      {
        title: I18n.t('MrkUser.middleName'),
        dataIndex: 'middleName',
        key: 'middleName',
        width: 200
      },
      {
        title: () => <UserAddOutlined style={{ cursor: 'pointer' }} />,
        dataIndex: 'deleteDate',
        align: 'center',
        width: 40,
        onHeaderCell: () => {
          return {
            onClick: () => showModal(MrkUserModal, {
              entity: new MrkUser({
                login: 'User_'
              }),
              onOk: createOrUpdate
            })
          };
        },
        onCell: (record) => {
          return {
            onClick: e => {
              e.stopPropagation();
              if (record.deleteDate === -1) remove(record.id);
            }
          };
        },
        render: (deleteDate) => deleteDate === -1 ? <DeleteOutlined style={{ cursor: 'pointer' }} /> : null
      }
    ]}
  />;
};

const mapStateToProps = state => ({
  loading: state.users.isFetching,
  data: state.users.data,
  count: state.users.count,
  filters: state.users.filters,
  sorter: state.users.sorter,
  page: state.users.page
});
const mapDispatchToProps = dispatch =>
  bindActionCreators(
    {
      getData,
      createOrUpdate,
      remove
    },
    dispatch
  );

export default connect(mapStateToProps, mapDispatchToProps)(Content);


