import {
  GET_DATA_REQUEST,
  GET_DATA_SUCCESS,
  GET_DATA_FAILURE,
  CREATE_OR_UPDATE_SUCCESS
} from 'redux/actions/users';

const initialState = {
  data: [],
  count: 0,
  page: 1,
  filters: {},
  sorter: {},
  isFetching: false
};

export default (state = initialState, action) => {
  switch (action.type) {
    case GET_DATA_REQUEST:
      return {
        ...state,
        isFetching: true
      };
    case GET_DATA_SUCCESS:
      return {
        ...state,
        data: action.payload.users,
        count: action.payload.count,
        page: action.payload.page,
        filters: action.payload.filters,
        sorter: action.payload.sorter,
        isFetching: false
      };
    case GET_DATA_FAILURE:
      return {
        ...state,
        isFetching: false
      };
    case CREATE_OR_UPDATE_SUCCESS:
      return {
        ...state,
        data: action.payload
      };
    default:
      return state;
  }
};
