import { notificationError } from 'utils/helpers';
import { TABLE_SIZE } from 'constants/table';
import { get } from 'lodash';

const PREFIX = 'Accounts';

export const GET_DATA_REQUEST = `${PREFIX}/GET_DATA_REQUEST`;
export const GET_DATA_SUCCESS = `${PREFIX}/GET_DATA_SUCCESS`;
export const GET_DATA_FAILURE = `${PREFIX}/GET_DATA_FAILURE`;

export const getData = (page = 1, filters = {}, sorter = {}) => {
  return async (dispatch, getState, api) => {
    dispatch({ type: GET_DATA_REQUEST });
    try {
      const {
        auth: { token }
      } = getState();
      const filter = new KazFilter({
        position: page * TABLE_SIZE - TABLE_SIZE,
        countFilter: TABLE_SIZE,
        orders: [],
        items: []
      });
      if (get(filters, 'name.0', null) !== null) filter.items.push(new FilterItem({
        field: 'name',
        value: get(filters, 'name.0', null),
        condition: FilterCondition.CONTAIN,
        fType: FilterFieldType.STRING
      }));
      if (get(filters, 'code.0', null) !== null) filter.items.push(new FilterItem({
        field: 'code',
        value: get(filters, 'code.0', null),
        condition: FilterCondition.CONTAIN,
        fType: FilterFieldType.STRING
      }));
      if (get(filters, 'organization.0', null) !== null) filter.items.push(new FilterItem({
        field: 'organization',
        value: get(filters, 'organization.0', null),
        condition: FilterCondition.EQUAL,
        fType: FilterFieldType.BOOLEAN
      }));
      if (get(filters, 'contragent.0', null) !== null) filter.items.push(new FilterItem({
        field: 'contragent',
        value: get(filters, 'contragent.0', null),
        condition: FilterCondition.EQUAL,
        fType: FilterFieldType.BOOLEAN
      }));
      if (get(filters, 'signed.0', null) !== null) filter.items.push(new FilterItem({
        field: 'signed',
        value: get(filters, 'signed.0', null),
        condition: FilterCondition.EQUAL,
        fType: FilterFieldType.BOOLEAN
      }));
      if (get(filters, 'blocked.0', null) !== null) filter.items.push(new FilterItem({
        field: 'blocked',
        value: get(filters, 'blocked.0', null),
        condition: FilterCondition.EQUAL,
        fType: FilterFieldType.BOOLEAN
      }));
      if (get(filters, 'confirmed.0', null) !== null) filter.items.push(new FilterItem({
        field: 'confirmed',
        value: get(filters, 'confirmed.0', null),
        condition: FilterCondition.EQUAL,
        fType: FilterFieldType.BOOLEAN
      }));
      if (get(filters, 'accType.0', null) !== null) filter.items.push(new FilterItem({
        field: 'accType',
        value: get(filters, 'accType.0', null),
        condition: FilterCondition.EQUAL,
        fType: FilterFieldType.ENUMERATED
      }));
      const result = await api.MrkAdminServiceClient.getMrkAggregateAccountInfoPage(token, filter);
      dispatch({
        type: GET_DATA_SUCCESS,
        payload: {
          ...result,
          page: page,
          filters: filters,
          sorter: sorter
        }
      });
    } catch (error) {
      notificationError(error, 'getMrkAggregateAccountInfoPage');
      dispatch({ type: GET_DATA_FAILURE });
    }
  };
};

export const CREATE_OR_UPDATE_REQUEST = `${PREFIX}/CREATE_OR_UPDATE_REQUEST`;
export const CREATE_OR_UPDATE_SUCCESS = `${PREFIX}/CREATE_OR_UPDATE_SUCCESS`;
export const CREATE_OR_UPDATE_FAILURE = `${PREFIX}/CREATE_OR_UPDATE_FAILURE`;

export const createOrUpdate = values => {
  return async (dispatch, getState, api) => {
    dispatch({ type: CREATE_OR_UPDATE_REQUEST });
    try {
      const {
        auth: { token },
        accounts: { page, filters, sorter }
      } = getState();
      await api.MrkAdminServiceClient.changeMrkAccount(token, new MrkAccount(values));
      dispatch({
        type: CREATE_OR_UPDATE_SUCCESS
      });
      dispatch(getData(page, filters, sorter));
      return true;
    } catch (error) {
      notificationError(error, 'changeMrkAccount');
      dispatch({ type: CREATE_OR_UPDATE_FAILURE });
      return false;
    }
  };
};