import React from 'react';
import SearchColumn from 'components/SearchColumn';
import FilterRadioColumn from 'components/FilterRadioColumn';
import { SearchOutlined } from '@ant-design/icons';

export const TABLE_SIZE = 20;

export const searchProps = (value = [], placeholder = '') => ({
  filterDropdown: (props) => <SearchColumn {...props} placeholder={placeholder} />,
  filterIcon: filtered => <SearchOutlined style={{ color: filtered ? '#1890ff' : undefined }} />,
  filterMultiple: false,
  filteredValue: value
});

export const filterRadioProps = (value = [], filters = []) => ({
  filterDropdown: (props) => <FilterRadioColumn {...props} />,
  filterMultiple: false,
  filters: filters,
  filteredValue: value
});