import React, { useState, useRef } from 'react';
import { Modal, notification } from 'antd';
import { I18n } from 'react-redux-i18n';
import { get } from 'lodash';
import { UserOutlined, LockOutlined } from '@ant-design/icons';
import { formLayout } from 'constants/form';
import { Formik } from 'formik';
import { Form, FormItem, Input } from 'formik-antd';
import { mrkUser } from 'constants/schemes';

const MrkUserModal = ({ show, hideModal, entity, onOk }) => {
  const [saving, setSaving] = useState(false);
  const form = useRef(null);

  return <Modal
    title={I18n.t('Modals.MrkUserModal')}
    visible={show}
    onOk={() => {
      form.current.handleSubmit();
      if (!form.current.isValid) notification.error({
        message: I18n.t('common.error'),
        description: I18n.t('common.form_is_invalid')
      });
    }}
    okButtonProps={{
      loading: saving
    }}
    onCancel={hideModal}
  >
    <Formik
      innerRef={form}
      initialValues={{
        ...entity,
        password: null
      }}
      validateOnMount={true}
      validationSchema={mrkUser()}
      onSubmit={async (values) => {
        try {
          setSaving(true);
          const result = await onOk(values);
          if (result) hideModal();
          setSaving(false);
        } catch (error) {
          setSaving(false);
        }
      }}
    >
      {({ values }) => <Form {...formLayout}>
        <FormItem
          name="login"
          label={I18n.t('MrkUser.login')}
          labelAlign="left"
          required
        >
          <Input
            name="login"
            prefix={<UserOutlined />}
            placeholder={I18n.t('MrkUser.login')}
          />
        </FormItem>
        <FormItem
          name="password"
          label={I18n.t('SignIn.password')}
          labelAlign="left"
          required={get(values, 'id', null) === null}
        >
          <Input.Password
            name="password"
            prefix={<LockOutlined />}
            placeholder={I18n.t('SignIn.password')}
          />
        </FormItem>
        <FormItem
          name="firstName"
          label={I18n.t('MrkUser.firstName')}
          labelAlign="left"
          required
        >
          <Input
            name="firstName"
            placeholder={I18n.t('MrkUser.firstName')}
          />
        </FormItem>
        <FormItem
          name="lastName"
          label={I18n.t('MrkUser.lastName')}
          labelAlign="left"
          required
        >
          <Input
            name="lastName"
            placeholder={I18n.t('MrkUser.lastName')}
          />
        </FormItem>
        <FormItem
          name="middleName"
          label={I18n.t('MrkUser.middleName')}
          labelAlign="left"
          required
        >
          <Input
            name="middleName"
            placeholder={I18n.t('MrkUser.middleName')}
          />
        </FormItem>
      </Form>}
    </Formik>
  </Modal>;
};

export default MrkUserModal;