import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import Table from 'components/Table';
import { notification } from 'antd';
import { getData, createOrUpdate, remove } from 'redux/actions/systemUsers';
import { TABLE_SIZE, searchProps, filterRadioProps } from 'constants/table';
import moment from 'moment';
import { uniqueId, map, get, invert } from 'lodash';
import { DeleteOutlined, UserAddOutlined } from '@ant-design/icons';
import { I18n } from 'react-redux-i18n';
import { showModal } from 'react-redux-modal-provider';
import { MrkAlmexSysUserModal } from 'components/Modals';
const Content = ({ data, count, page = 1, filters, sorter, loading, getData, createOrUpdate, remove }) => {

  useEffect(() => {
    getData();
  }, []);

  return <Table
    dataSource={data}
    count={count}
    loading={loading}
    rowKey={() => uniqueId('rowKey_')}
    pagination={{
      defaultPageSize: TABLE_SIZE,
      total: count,
      current: page,
      onChange: (page) => getData(page, filters, sorter)
    }}
    onChange={(_, filters, sorter) => getData(1, filters, sorter)}
    onRow={(record) => {
      const key = uniqueId('notification_');
      return {
        onClick: () => record.deleteDate !== -1 ? notification.info({
          key: key,
          duration: 3,
          message: I18n.t('notification.info'),
          description: I18n.t('notification.MrkAlmexSysUser_removed', { name: record.login })
        }) : showModal(MrkAlmexSysUserModal, {
          entity: record,
          onOk: createOrUpdate
        })
      };
    }}
    columns={
      [
        {
          title: I18n.t('MrkAlmexSysUser.login'),
          dataIndex: 'login',
          key: 'login',
          ...searchProps(get(filters, 'login', null), I18n.t('MrkAlmexSysUser.login'))
        },
        {
          title: I18n.t('MrkAlmexSysUser.createDate'),
          dataIndex: 'createDate',
          align: 'center',
          width: 120,
          render: date => moment(date).format('DD.MM.YYYY')
        },
        {
          title: I18n.t('MrkAlmexSysUser.allowedForType'),
          dataIndex: 'allowedForType',
          align: 'center',
          width: 100,
          render: allowedForType => I18n.t(`MrkAccountType.${invert(MrkAccountType)[allowedForType]}`),
          ...filterRadioProps(get(filters, 'allowedForType', null), map(MrkAccountType, (value, key) => ({ text: I18n.t(`MrkAccountType.${key}`), value: `MrkAccountType.${key}` })))
        },
        {
          title: I18n.t('MrkAlmexSysUser.usePriority'),
          dataIndex: 'usePriority',
          align: 'center',
          width: 100,
          key: 'usePriority'
        },
        {
          title: () => <UserAddOutlined style={{ cursor: 'pointer' }} />,
          dataIndex: 'deleteDate',
          align: 'center',
          width: 40,
          onHeaderCell: () => {
            return {
              onClick: () => showModal(MrkAlmexSysUserModal, {
                entity: new MrkAlmexSysUser({
                  login: 'Broker_',
                  usePriority: 1,
                  allowedForType: MrkAccountType.LEGAL_CONFIRMED
                }),
                onOk: createOrUpdate
              })
            };
          },
          onCell: (record) => {
            return {
              onClick: e => {
                e.stopPropagation();
                if (record.deleteDate === -1) remove(record.id);
              }
            };
          },
          render: (deleteDate) => deleteDate === -1 ? <DeleteOutlined style={{ cursor: 'pointer' }} /> : null
        }
      ]}
  />;
};

const mapStateToProps = state => ({
  loading: state.systemUsers.isFetching,
  data: state.systemUsers.data,
  count: state.systemUsers.count,
  filters: state.systemUsers.filters,
  sorter: state.systemUsers.sorter,
  page: state.systemUsers.page
});
const mapDispatchToProps = dispatch =>
  bindActionCreators(
    {
      getData,
      createOrUpdate,
      remove
    },
    dispatch
  );

export default connect(mapStateToProps, mapDispatchToProps)(Content);


